import uniRate from "@/components/common/rate/uni-rate/uni-rate.vue"
import { requestWB as uniRequest, requestZT as ztRequest } from 'uni-request';

export default {
	components: {
		uniRate,
	},
	props: {
		/**
		 * 页面显示
		 */
		id:String,
		show: {
			type: Boolean,
			default: false
		},

		/**
		 * 额外信息
		 */
		msg: {
			type:String,
			default:''
		},
		/**
		 * h5遮罩是否到顶
		 */
		h5Top: {
			type: Boolean,
			default: false
		},
		buttonMode: {
			type: String,
			default: 'bottom'
		}
	},
	data() {
		return {
			offsetTop: 0,
			TextAreaBlur:'',
			RateValue:0,
			list:[],
			ple:false
		}
	},
	watch: {
		h5Top(newVal) {
			if (newVal) {
				this.offsetTop = 44
			} else {
				this.offsetTop = 0
			}
		}
	},
	created() {
		let offsetTop = 0







		this.offsetTop = offsetTop
	},
	methods: {
		hide() {
			this.$emit('hidePopup')
		},
		closeMask() {
			this.$emit('hidePopup')
		},
		// 拨打电话
		callPhone(msg){
			uni.makePhoneCall({
				phoneNumber: msg 
			});
		},
		// 取消评价，数据清空
		cancel(){
			this.RateValue=0,
			this.TextAreaBlur=''
			this.$emit('hidePopup')
		},
		rate: function (e){
						this.RateValue=e.value
					},
					// 文本输入
		bindTextAreaBlur: function (e) {
				this.TextAreaBlur=e.detail.value
		},
		// 确认提交
		affirm(){
			
			if(this.RateValue==0){
				uni.showToast({
						title: '请选择评分',
						duration: 1000,
						position:'center'
				});
			}else if(!this.TextAreaBlur){
				uni.showToast({
						title: '请输入评价内容',
						duration: 1000,
						position:'center'
				});
			}else {
				const star=this.RateValue
				const val=this.TextAreaBlur
				let _this=this

				my.getAuthCode({
					scopes: 'auth_user',
					success: (res) => {
						if (res.authCode) {
							// console.log(res);
						    	uni.getUserInfo({
								provider: 'alipay',
								success: function(infoRes) {
									uniRequest.post('/tourguidecomment/addtourguidecomment', {
										tourguideid:_this.id,
										score:star,
										content:val,
										commentator:infoRes.userInfo.nickName,
										avatar:infoRes.userInfo.avatarUrl,
										type:1
									}).then(function(res) {
										_this.$emit("affirm",_this.id)

									})
									.catch(function(error) {
										console.log(error);
									});
									
								}
							});
						}
					},
				});


			}
			this.RateValue=0,
			this.TextAreaBlur=''
		},
		
		 
	}
}
